import React from 'react'

export const Form = ({inputText,setInputText, setTodos, todos, setStatus}) => {
    const inputTextHandler = (e) =>{
        setInputText(e.target.value);
    }
    const submitTodoHandler = (e) =>{
        e.preventDefault();
        setTodos([
            ...todos, {text:inputText, completed: false, id: Math.random() * 1000}
        ]);
        setInputText("");
    }
    const statusHandler = (e) => {
        setStatus(e.target.value);
    }
    return (
        <center>
            <div className="form">
                <form>
                    <div className="seventy">
                        <input value={inputText} onChange={inputTextHandler} type="text" className="input"></input>
                        <button onClick={submitTodoHandler} className="btn" type="submit">
                            <i className="fa fa-plus" aria-hidden="true"></i>
                        </button>
                    </div>
                    <div className="thirty">
                        <select onChange={statusHandler} name="todos" className="filter">
                            <option value="all">All</option>
                            <option value="completed">Completed</option>
                            <option value="uncompleted">Uncomplete</option>
                        </select>
                    </div>
                    <div className="clear"></div>
                </form>
            </div>
        </center>
    )
}
