import React from 'react'

export const Todo = ({text,setTodos,todos,todo}) => {
    const deleteHandler = () => {
        setTodos(todos.filter(el => el.id !== todo.id))
    }
    const completeHandler = () => {
        setTodos(
            todos.map((item)=>{
                if (item.id === todo.id && todo.completed === false){
                    return{
                        ...item,
                        completed: !item.completed,
                    };
                }
                return item;
            }
            )
        )
    }
    return (
        <div className="todo">
            
            <li className={todo.completed?"todo-item completed":"todo-item"}> {text}</li>
            <button onClick={completeHandler} className="check">
                <i className="fa fa-check" aria-hidden="true"></i>
                </button>
            <button className="del" onClick={deleteHandler}>
            <i className="fa fa-times" aria-hidden="true"></i>
            </button>
            <div className="clear"></div>
            
        </div>
    )
}
